export default {
	data() {
		return {
			isok: true,
			isWithDraw:false,
			count:'',
			timer:'',
		};
	},
	methods:{
	      submitWithDraw(){
			this.isWithDraw=true;
		},
		getCode() {
			const TIME_COUNT = 60;
			let timer,count;
			if (!this.timer) {
				this.count = TIME_COUNT;
				this.timer = setInterval(() => {
					if (this.count > 0 && this.count <= TIME_COUNT) {
						this.count--;
					} else {
						clearInterval(this.timer);
						this.timer = null;
					}
					return count;
				}, 1000);
			}
		},
	}
};
